import React from "react";
import makeStyles from "@material-ui/core/styles/makeStyles";
import { getImage } from "./components/ImageGetter";

const useStyles = makeStyles({
  root: {
    position: "fixed",
    width: "100%",
    height: "100%",
    top: "0",
    left: "0",
    background: "#fff",
  },
  img: {
    width: "100%",
    height: "100%",
    background:
      "url(../../../assets/img/quick-record-web.gifz) center center/contain no-repeat",
  },
});

// https://developer.mozilla.org/en-US/docs/Web/Media/Formats/Image_types
const MediaType = {
  Image:
    /\.(png|apng|gif|svg|webp|tif|tiff|bmp|ico|cur|avif|jpg|jpeg|jfif|pjpeg)/i,
  Audio: /\.(mp3)/i,
  Video: /\.(mp4|webm|ogv|ogm|ogg|avi)/i,
  Embed: /.*/i,
};

const mediaRenderers = (type, classes, url) => {
  switch (type) {
    case MediaType.Image:
      return (
        <div
          style={{ backgroundImage: `url(${getImage(url)})` }}
          className={classes.img}
        />
      );
    case MediaType.Audio:
      return (
        <audio src={url}>
          <track
            default
            kind="captions"
            srcLang="en"
            src="/media/examples/friday.vttz"
          />
        </audio>
      );
    case MediaType.Video:
      return (
        <video src={url}>
          <track
            default
            kind="captions"
            srcLang="en"
            src="/media/examples/friday.vtt"
          />
        </video>
      );
    case MediaType.Embed:
      return <img src={url} alt="" className={classes.img} />;
    default:
      return;
  }
};

const detectMediaType = (url = "") => {
  const detectedPattern = Object.values(MediaType).find((pattern) =>
    pattern.test(url)
  );
  return detectedPattern != null ? detectedPattern : MediaType.Image;
};

export default function MultiMediaPage() {
  const classes = useStyles();

  const url = new URL(window.location.href);
  const mediaUrl = url.searchParams.get("url");
  const detectedPattern = detectMediaType(mediaUrl);

  return (
    <div className={classes.root}>
      {mediaRenderers(detectedPattern, classes, mediaUrl)}
    </div>
  );
}
